  align-items: center;
  justify-content: center;
  z-index: 2;
}

#Heading{

  z-index: 1;
  padding: 10px;
}


.LogoP{
  font-family:Impact;  
  font-size: 9rem;
  
}

.LogoSquare{
  font-family: Pacifico;  
  
}


