  margin: 0;
  background-size: cover;  
  z-index: 1;
  /* align-items: center; */
  /* justify-content: center; */

}

.parallax{
  min-height: 100%;
  background-attachment: fixed;
  /* background-position: center; */
  background-repeat: no-repeat;
  background-size: cover;
  
}

p{
  color:white;
  margin: 0;
  
  
}

menu{

  color:rgb(0, 0, 0);
  margin: 0;
  float: right;
  /* text-align: right; */
  z-index: 3;
  flex-wrap: wrap;
  
}



#titleSquare.tracking-in-contract-bck-top {
	animation: tracking-in-contract-bck-top 1.5s cubic-bezier(0.215, 0.610, 0.355, 1.000) 1s both;
}

#titleProject.tracking-in-contract-bck-top {
	animation: tracking-in-contract-bck-top 1.5s cubic-bezier(0.215, 0.610, 0.355, 1.000) 2s both;
}

/* Title animation */
@keyframes tracking-in-contract-bck-top {
  0% {
    letter-spacing: 1em;
    transform: translateZ(400px) translateY(-300px);
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    transform: translateZ(0) translateY(0);
    opacity: 1;
  }
}

